.shop-header {

    h2{
    text-align: center;
    font-size: 24px;
    letter-spacing: 0.5rem;
    margin-bottom: 20px;

    }
}
//shop navigation

.product-nav {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-decoration: none;
    
    li {
        padding: 0px 20px;
        
    }
}



.shop-picture {

    // display: flex;
    // flex-direction: row;
    // flex-flow: column nowrap;
 
    display: grid;
    grid-template-columns: 20% 20% 20% 20%;
    grid-template-rows: 20% 20% 20% 20%;
    grid-gap: 20px;
    justify-content: center;

    img {
        width: 200px;
        height: 200px;   
        filter: none;     
    }
    
    // grid-template-columns:  20% 20% 20% 20%;
    // grid-template-rows: 20% 20% 20% 20%;



   
}

//Product Type archive starts here

.product-type-header {
    text-align: center;
    margin-bottom: 40px;
    border-bottom: 1px dashed #c8c8c8;
    

    h2 {
        font-size: 24px;
        padding: 20px;
    }
    p{
        margin-bottom: 30px;
    }
}

.product-type-content {
    
    display: grid;
    // width: 90%;
    // justify-content: center;
    grid-template-columns:  repeat(4, 1fr);
    
    grid-gap: 20px;
   

    // height: 300px;
    // grid-gap: 20px;
    // justify-content: center;
//    width: 200px;
//    height: 200px;
//    grid-template-columns: 1fr 1fr 1fr 1fr;
    img {
        width: 100%;
        height: 300px;
        filter: none;
    }

    figcaption {
        display: grid;
        // width: 85%;
        grid-template-columns: 1fr;
        padding: 10px;
        border: 1px solid #e1e1e1;
        margin-top: -10px;
        border-top: 5px solid #e1e1e1;
        

        h2 {
            text-decoration: none;
        
        }
    }
}

