//single-page starts here

.single-page {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 90px;

    img {
        width: 100%;
        height: 360px;
        margin-left: 45px;
        filter: none;
    }
}

.single-page-contents {
    h2 {
        font-size: 24px;
        color: #d99054;
        text-transform: uppercase;
        letter-spacing: .3rem;
    }

    h1 {
        font-size: 20px;
        font-family: $nav-font;
        padding: 20px 0px;

    }
}

.single-products-social {
    display: grid;
    grid-template-columns: 100px 150px 100px;
    grid-gap: 5px;
    margin-top: 20px;


    li {
        text-align: center;
        border: 1px solid black;
        padding: 10px;
        text-transform: uppercase;
        font-family: $nav-font;
    }
}

//single-products end here